___CSS_LOADER_EXPORT___.push([module.id, `button {
  cursor: pointer;
  transition: opacity 0.2s ease;
}

.btn {
  padding: 0.5rem 1.5rem;
  border: none;
  border-radius: 1000px;
  background: transparent;
  color: var(--primary-contrast);
}

.filled-btn {
  background-color: var(--secondary);
  color: white;
}

.raised-btn {
  box-shadow: 0px 0px 10px var(--primary-contrast);
}

.outlined-btn {
  border: 1px solid var(--primary-contrast);
}

.icon-btn {
  width: 35px;
  height: 35px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--tertiary);
  border-radius: 100%;
  border: 0;
  fill: var(--primary-contrast);
}

.nav-btn {
  text-align: left;
  width: 100%;
  height: 100%;
  padding-block: calc(var(--m-gap) / 1.25);
  background-color: var(--primary);
}

.selected-nav-btn {
  background-color: var(--primary-contrast);
  color: var(--primary);
}

#cancel-delete-btn {
  background-color: red;
  width: 30px;
  height: 30px;
  position: absolute;
  top: 8px;
  right: 8px;
}

#cancel-delete-btn svg {
  width: 80%;
}

.icon-btn:hover {
  opacity: 0.6;
}

.create-task-btn {
  border-radius: 25px;
  border: 0;
  padding-block: var(--m-gap);
}

@media (max-width: 1220px) {
  .nav-btn {
    text-align: center;
  }
}
