  .tasks-container {
    display: flex;
    flex-direction: column;
    background-color: var(--primary-lighter);
    border-radius: 20px;
    padding: var(--l-gap);
  }

  .tasks-container > :nth-last-child(2) {
    margin-bottom: var(--l-gap);
  }

  .tasks-container button {
    margin-top: auto;
  }

  .draggable {
    cursor: move;
  }

  .dragging {
    opacity: 0.7;
  }

  .highlight-drag-area {
    outline: 2px solid var(--primary-contrast);
    border-radius: 20px;
  }

  @media() {
  }
}
